  /* Modern displayType */
  .modernProfile {
    width: 225px;
    height: 225px;
    margin: 25px auto;
    overflow: hidden;
    border-radius: 50%;
    position: relative;
    background-color: whitesmoke;
  }
  
  .modernProfile img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 240px;
  }
  
  .ch-img-1 {
    /* position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); */
    max-width: 180px;
    background-size: cover;
  }
  
  .modernCol {
    padding: 34px;
  }
  
  .ch-item {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    border-radius: 50%;
    position: relative;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
    cursor: default;
  }
  
  .ch-info-wrap {
    /* position: absolute; */
    width: 180px;
    height: 180px;
    border-radius: 50%;
    perspective: 800px;
    transition: all 0.4s ease-in-out;
    top: 20px;
    left: 20px;
    box-shadow: 0 0 0 20px rgba(241, 241, 241, 0.8),
      inset 0 0 3px rgba(241, 241, 241, 0.8);
  }
  
  .ch-info {
    position: absolute;
    width: 180px;
    height: 180px;
    border-radius: 50%;
    transition: all 0.4s ease-in-out;
    transform-style: preserve-3d;
  }
  
  .ch-info > div {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-position: center center;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
  }
  
  .ch-info .ch-info-back {
    transform: rotate3d(0, 1, 0, 180deg);
    background: #000;
  }
  
  .ch-info h3 {
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 14px;
    margin: 0 30px;
    padding: 40px 20px 0 0;
    height: 90px;
    font-family: "Open Sans", Arial, sans-serif;
    text-shadow: 0 0 1px #fff, 0 1px 2px rgba(0, 0, 0, 0.3);
  }
  
  .ch-info p {
    color: #fff;
    padding: 10px 5px;
    font-style: italic;
    margin: 0 30px;
    font-size: 12px;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
  }
  
  .ch-info p a {
    display: block;
    color: rgba(255, 255, 255, 0.7);
    font-style: normal;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 9px;
    letter-spacing: 1px;
    padding-top: 4px;
    font-family: "Open Sans", Arial, sans-serif;
  }
  
  .ch-info p a:hover {
    color: rgba(255, 242, 34, 0.8);
  }
  
  .ch-item:hover .ch-info-wrap {
    box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.8),
      inset 0 0 3px rgba(115, 114, 23, 0.8);
  }
  
  .ch-item:hover .ch-info {
    transform: rotate3d(0, 1, 0, -180deg);
  }
  
  [data-toggle="modal"] {
    cursor: pointer;
  }
  
